.item-link-component {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  height: 300px;
  margin: 1rem;
  padding: 1rem;
  text-decoration: none;
  color: black;
  transition-property: filter;
  transition-duration: 300ms;

  @media (max-width: 320px) {
    margin: 0;
    padding: 0;
  }

  &:hover {
    filter: grayscale(100%);
  }

  .icone {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
    margin-bottom: 1rem;
  }

  .title {
    text-align: center;
  }
}
