.index-page {
  .background {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    &::before {
      content: "";
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background-image: url("../../assets/imgs/background.jpg");
      z-index: -1;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      filter: blur(4px);
    }
  }

  .form {
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    padding: 4rem;
    border-radius: var(--default-radius);
    border: 1px solid rgba(255, 255, 255, 0.3);
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    .title {
      font-family: var(--merienda-font);
      font-size: 3rem;
      color: var(--gold-color);
      margin-bottom: 1rem;
    }

    .span {
      font-size: 1.5rem;
    }

    .buttons {
      position: absolute;
      bottom: 0;
      width: 100%;
      display: flex;
    }

    .button {
      flex-grow: 1;
      color: white;
      text-decoration: none;
      text-align: center;
      padding-top: 0.5rem;
      padding-bottom: 0.5rem;
      background-color: black;
      transition-property: color, background-color;
      transition-duration: 300ms;
      border: none;
      cursor: pointer;
      font-size: 1rem;
      font-weight: 400;

      &:first-child {
        border-bottom-left-radius: var(--default-radius);
        border-right: 1px solid rgba(255, 255, 255, 0.3);
      }

      &:last-child {
        border-bottom-right-radius: var(--default-radius);
      }

      &:hover {
        color: black;
        background-color: white;
      }
    }
  }

  .sobre {
    animation-name: index-page__sobre-animation;
    animation-duration: 400ms;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 9;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    color: white;

    .sobre-central {
      --index-page-sobre-width-base: 60vw;
      display: flex;
      width: var(--index-page-sobre-width-base);
      padding: 1rem;
      --index-page-sobre-item-width: calc(
        var(--index-page-sobre-width-base) / 3
      );
      justify-content: space-between;

      h2 {
        margin-bottom: 0.6rem;
        font-weight: bold;
      }
    }

    .times {
      position: fixed;
      top: 2rem;
      right: 2rem;
      color: white;
      font-size: 3rem;
      cursor: pointer;
      user-select: none;
    }

    .sobre-text {
      width: var(--index-page-sobre-item-width);
      text-align: justify;
    }

    .sobre-contato {
      width: var(--index-page-sobre-item-width);
    }

    .contato {
      margin-top: 2rem;
    }

    .contato-link {
      filter: invert(100%) sepia(20%) saturate(0%) hue-rotate(282deg)
        brightness(104%) contrast(104%);
      width: 3rem;
      height: 3rem;
      margin-right: 1rem;
      transition-property: filter;
      transition-duration: 300ms;
    }

    .whatsapp:hover .contato-link {
      filter: invert(72%) sepia(100%) saturate(472%) hue-rotate(35deg)
        brightness(100%) contrast(111%);
    }

    .instagram:hover .contato-link {
      filter: invert(16%) sepia(75%) saturate(6573%) hue-rotate(355deg)
        brightness(93%) contrast(104%);
    }

    .facebook:hover .contato-link {
      filter: invert(46%) sepia(88%) saturate(1435%) hue-rotate(163deg)
        brightness(98%) contrast(101%);
    }

    .email:hover .contato-link {
      filter: invert(83%) sepia(68%) saturate(1485%) hue-rotate(3deg)
        brightness(108%) contrast(105%);
    }
  }
}

@media (max-width: 1680px) {
  .index-page .sobre .sobre-central {
    --index-page-sobre-width-base: 70vw;
  }
}

@media (max-width: 1400px) {
  .index-page .sobre .sobre-central {
    --index-page-sobre-width-base: 80vw;
  }
}

@media (max-width: 1200px) {
  .index-page .sobre .sobre-central {
    --index-page-sobre-width-base: 90vw;
  }
}

@media (max-width: 1090px) {
  .index-page .sobre .sobre-central {
    --index-page-sobre-width-base: 100vw;
  }
}

@media (max-width: 990px) {
  .index-page .sobre {
    .sobre-central {
      --index-page-sobre-item-width: 400px;
      flex-direction: column;
      height: 90vh;
      overflow-y: auto;
      overflow-x: hidden;
      width: 400px;
      align-items: center;

      div {
        margin-bottom: 2rem;
      }
    }

    .sobre-localizacao {
      display: inline-block;
    }
  }
}

@media (max-width: 690px) {
  .index-page .form {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 0;
    border-radius: 0;
    justify-content: center;

    .button {
      &:first-child {
        border-bottom-left-radius: 0;
      }

      &:last-child {
        border-bottom-right-radius: 0;
      }
    }
  }
}

@media (max-width: 560px) {
  .index-page .form {
    .title {
      font-size: 2rem;
    }

    .span {
      font-size: 1rem;
    }
  }
}

@media (max-width: 550px) {
  .index-page .sobre .times {
    top: 0.5rem;
    right: 0.5rem;
  }
}

@media (max-width: 416px) {
  .index-page .sobre {
    .sobre-central {
      --index-page-sobre-item-width: 290px;
      width: 290px;
    }

    .sobre-localizacao iframe {
      width: 290px;
      height: 290px;
    }
  }
}

@media (max-width: 390px) {
  .index-page .form {
    .title {
      font-size: 1.5rem;
    }

    .span {
      font-size: 1rem;
    }
  }
}

@keyframes index-page__sobre-animation {
  from {
    top: -100vh;
  }
  to {
    top: 0;
  }
}
