.produto-page {
  --shared-width: 900px;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 4rem;
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 1rem;
    margin-top: 1rem;
  }

  .config-carrousel {
    width: var(--shared-width);
    height: 100%;
    margin-bottom: 1rem;
  }

  h2 {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 2rem;
    margin-bottom: 1rem;
  }

  p {
    font-size: 1.4rem;
    width: var(--shared-width);
    text-align: justify;
    white-space: pre-line;
  }

  .whatsapp {
    height: 50px;
    width: 250px;
    position: relative;
    background-color: rgba(0, 0, 0, 0.6);
    border: 1px solid gray;
    transition-property: transform;
    transition-duration: 300ms;
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-top: 3rem;
    text-decoration: none;
    user-select: none;
    margin-bottom: 3rem;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;

    &:hover {
      transform: scale(1.1);
    }

    img {
      filter: invert(29%) sepia(17%) saturate(2001%) hue-rotate(72deg)
        brightness(93%) contrast(91%);
      height: 30px;
      width: 30px;
      top: 9px;
      left: 1px;
      position: absolute;
    }

    &::before {
      content: " ";
      background-color: white;
      top: -5px;
      left: -15px;
      border-radius: 50%;
      border: 1px solid gray;
      position: absolute;
      width: 60px;
      height: 60px;
    }

    span {
      color: white;
      font-size: 1 rem;
      margin-left: 5rem;
    }
  }

  &.center {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 940px) {
  .produto-page {
    --shared-width: 100vw;

    h1 {
      font-size: 10vw;
    }
  }
}
